import React, {useState} from 'react';
import './result.css';

const Result = ({name,src_img, voter, percentage}) => {

	const percentage_bar_style = {
		display: 'inline-block',
		height: '5vh',
		width: `${percentage*61/100}vw`,
		backgroundColor: 'powderblue',
		borderTopLeftRadius : '5px',
		borderBottomLeftRadius: '5px',
	}

	return (
		<React.Fragment>
			<div className="result">
				<div className="result__img-wrapper">
					<img className="result__img" src={src_img} alt=""/>
				</div>
				<div className="result__statistic">
					<div className="result__text">
						<p className="result__name-winner">{name}</p>
						<div className="result__number">
							<p className="total-voter">{`${voter} voter`}</p>
							<p className="percentage-number">{`${percentage}%`}</p>
						</div>
					</div>
					<div className="result__bar">
						<span className = "percentage-bar" style={percentage_bar_style} ></span>
					</div>
				</div>
			</div>
		</React.Fragment>
	)
}

export default Result;